@import '../../base_styles';

.card {
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: $whiteColor;

  width: 288px;
  padding: 32px;
  border: 1px solid $elementsColor;

  @include onTablet {
    height: 506px;
  }

  @include onLaptop {
    width: 230px;
  }

  @include onDesktop {
    width: 272px;
  }

  &__icon {
    transition: transform $transitionSettings;
  }

  &__img {
    display: block;
    width: 100%;
    height: 208px;
    object-fit: contain;
    object-position: center;
    overflow: hidden;

    margin-bottom: 24px;
  }

  &__name {
    @extend %BodyTextTitle;

    color: $primaryColor;
    margin-bottom: 8px;
  }

  &__price {
    display: flex;
    color: $primaryColor;
    @extend %H3Text;

    &__old {
      color: $secondaryColor;
      text-decoration: line-through;
      margin-left: 8px;
      font-family: 'Mont-Regular';
    }
    margin-bottom: 8px;
  }

  &__separator {
    width: 100%;
    border-bottom: 1px $elementsColor solid;
    margin-bottom: 8px;
  }

  &__params {
    display: flex;
    flex-direction: column;
    @extend %BodyText;
    font-family: 'Mont-Regular';

    &__container {
      margin: 0;
      display: flex;
      justify-content: space-between;

      padding: 0;
      line-height: 100%;
      margin-bottom: 8px;
    }

    &__text {
      color: $secondaryColor;
    }

    &__num {
      color: $primaryColor;
    }

    margin-bottom: 8px;
  }

  &__buy {
    display: flex;
    gap: 8px;

    &__add {
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      box-sizing: border-box;
      @extend %ButtonsText;

      background-color: $primaryColor;
      flex-grow: 1;
    }

    &__heart {
      height: 40px;
      width: 40px;

      background-color: $whiteColor;
      border: 1px solid $iconsColor;

      cursor: pointer;

      .heartIcon {
        width: 15px;
        height: 13px;

        transition: transform $transitionSettings;
      }

      &:hover .heartIcon {
        transform: scale(1.3);
      }

      &:active .heartIcon {
        transform: scale(0.8);
      }
    }
  }
}
